.main{
  margin-top: 50px;
  .inner{
    width: 1280px; margin: 0 auto;
    display: flex;
    gap: 20px;
    height: 800px;
    .box1, .box2{
      flex: 1;
      display: flex;
      justify-content: center;
      align-items: center;
    }
    .box2{
      display: flex; flex-direction: column; gap: 20px;
      .notice,.ref{
        flex: 1;
        width: 100%; height: 100%;
      }
    }
  }
}