.visual{
  width: 100%;
  height: 100%;
  background-size: cover;
  border-radius: 30px;
  box-shadow: 3px 3px 10px 5px rgb(173, 173, 173);
  .textWrap{
    display: flex;
    flex-direction: column;
    text-align: left;
    gap: 10px;
    transform: translateY(600px);
    margin-left: 50px;
    h2{color: white; font-size: 24px; font-weight: bold;}
    p{color: white; font-size: 18px;}
  }

}