.notice{
  width: 100%; height: 100%;
  background-color: white;
  .tabs{
    display: flex;
    justify-content: center;
    gap: 50px;
    font-size: 32px;
    font-weight: bold;

    li{
      cursor: pointer;
      &.isActive{
        border-bottom: 3px solid rgb(129, 129, 255);
      }
    }
  }
  .tabsWraper{
    margin-top: 30px;
    div{
      display: flex;
      flex-direction: column;
      text-align: left;
      gap: 20px;
      font-size: 20px;
      text-indent: 20px;
    }
  }
}